  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  display: none;
}

.modal {
  width: 100%;
  max-width: 280px;
  max-height: 95vh;
  background-color: #fff;
  flex-direction: column;
  padding: 48px 20px 40px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%)scale(1);
}

.modal::-webkit-scrollbar {
  width: 0;
}

@media screen and (min-width: 768px) {
  .modal {
    max-width: 704px;
